/* src/assets/styles/layout/_page.scss  */

.page {
    &__section {
        position: relative;
        padding-bottom: 44px;
        //padding: 32px 0 44px;

        &::before {
            content: '';
            position: absolute;
            z-index: -1;
            top: 0;
            bottom: 0;
            left: -50vw;
            right: -50vw;
            background: #fff;
        }

        &.__blue {
            &:before {
                background: $blue;
            }
        }

        &.__gray {
            &:before {
                background: $gray;
            }
        }

        //@include respond-to-min($dt) {
        //    padding: 34px 0 44px;
        //}

        &.__pad-0 {
            padding-top: 0;
            padding-bottom: 0;
        }
    }

    &__title {
        padding: 13px 0 27px;

        h1 {
            color: $black;
            font: 36px $fontGilroyExtraBold;
        }

        @include respond-to-min($tab) {

            h1 {
                font-size: 48px;
            }
        }
    }

    &__description {
        color: $blue;
        font-size: 18px;
        margin-bottom: 20px;
        padding-top: 32px;
    }
}

