/* src/assets/styles/components/_specifications.scss  */

.specifications_block {

    > ul {
        color: #7c7c7c;
        padding: 0;
        list-style: none;
        font-size: 18px;
        line-height: 1.35;

        @include respond-to-max($tab) {
            font-size: 16px;
        }

        > li {
            &:not(:last-child) {
                margin-bottom: .8em;
            }

            > ol {
                display: -webkit-box;
                display: -webkit-flex;
                display: -ms-flexbox;
                display: flex;
                -webkit-box-align: end;
                -webkit-align-items: flex-end;
                -ms-flex-align: end;
                align-items: flex-end;

                > li {
                    width: 50%;

                    i {
                        display: block;
                        font-style: normal;
                    }

                    &:first-child {
                        font-weight: 700;
                        color: #000;
                        position: relative;
                        z-index: 1;

                        > b {
                            background: #fff;
                        }

                        @include respond-to-min($mob) {

                            &::after {
                                content: '';
                                display: block;
                                width: 96%;
                                position: absolute;
                                left: 2%;
                                bottom: .25em;
                                z-index: -1;
                                border-bottom: 1px dotted #b9b9b9;
                            }
                        }
                    }
                }

                @include respond-to-max($mob) {
                    display: block;
                }
            }
        }
    }
}

.specifications {

    &__holder {
        margin-bottom: 40px;

        article {
            font-size: 16px;
            line-height: 1.5em;
            letter-spacing: normal;
            word-spacing: normal;
        }

        @include respond-to-min($wide) {
            display: flex;
            margin: 0 -25px;
            flex-flow: row nowrap;

            article {
                padding: 0 25px;
                width: 33.333%;
            }
        }

        div {
            margin-top: 30px;

            h4 {
                color: #1c70b7;
                font-size: 1.2em;
            }
        }
    }

    ul {
        li {
            list-style-type: disc;
            margin-left: 18px;
            margin-top: 2px;
        }
    }
}

