@if $local_var != onload {

    %trinity_icons_before {
        &::before {
            font-family: '#{$icomoon-font-family}' !important;
            speak: never;
            font-style: normal;
            font-weight: normal;
            font-variant: normal;
            text-transform: none;
            line-height: 1;

            /* Better Font Rendering =========== */
            -webkit-font-smoothing: antialiased;
            -moz-osx-font-smoothing: grayscale;
        }
    }

    %global_flex_block_e {
        @include flex_block();
    }

    %global_flex_block_row_wrap_flex-start {
        @include flex_block(row wrap, flex-start);
    }

    %global_flex_block_row_wrap_center {
        @include flex_block(row wrap, center);
    }

    %global_flex_block_row_nowrap_flex-start {
        @include flex_block(row nowrap, flex-start);
    }

    %global_flex_block_row_nowrap_flex-end {
        @include flex_block(row nowrap, flex-end);
    }

    %global_flex_block_row_nowrap_center {
        @include flex_block(row nowrap, center);
    }

    %global_flex_block_row_nowrap_space-between {
        @include flex_block(row nowrap, space-between);
    }

    %global_flex_block_row_wrap_space_between_stretch {
        @include flex_block(row wrap, space-between, stretch);
    }

    %global_flex_block_row_wrap_space_between {
        @include flex_block(row wrap, space-between);
    }

    %global_flex_block_row_nowrap_space_between_stretch {
        @include flex_block(row nowrap, space-between, stretch);
    }

    %global_flex_block_row_wrap_flex-start_stretch {
        @include flex_block(row wrap, flex-start, stretch);
    }

    %global_flex_block_row_nowrap_flex-start_stretch {
        @include flex_block(row nowrap, flex-start, stretch);
    }

    %global_flex_block_row_wrap_flex-end_stretch {
        @include flex_block(row nowrap, flex-end, stretch);
    }

    %global_flex_block_row_wrap_space_between_center {
        @include flex_block(row wrap, space-between, center);
    }

    %global_flex_block_row_wrap_space_around_center {
        @include flex_block(row wrap, space-around, center);
    }

    %global_flex_block_row_nowrap_space_between_center {
        @include flex_block(row nowrap, space-between, center);
    }

    %global_flex_block_row_nowrap_flex_end_center {
        @include flex_block(row nowrap, flex-end, center);
    }

    %global_flex_block_row_nowrap_flex_end_stretch {
        @include flex_block(row nowrap, flex-end, stretch);
    }

    %global_flex_block_row_nowrap_flex_start_center {
        @include flex_block(row nowrap, flex-start, center);
    }

    %global_flex_block_row_nowrap_center_center {
        @include flex_block(row nowrap, center, center);
    }

    %global_flex_block_column_nowrap_center_center {
        @include flex_block(column nowrap, center, center);
    }

    %global_flex_block_column_nowrap_center_stretch {
        @include flex_block(column nowrap, center, stretch);
    }

    %global_flex_block_column_nowrap_flex-start {
        @include flex_block(column nowrap, flex-start);
    }

    %global_flex_block_column_nowrap_flex-start_stretch {
        @include flex_block(column nowrap, flex-start, stretch);
    }

    %global_flex_block_column_nowrap_space_between_stretch {
        @include flex_block(column nowrap, space-between, stretch);
    }

    %global_flex_block_column_nowrap_space_around_stretch {
        @include flex_block(column nowrap, space-around, stretch);
    }

    %global_flex_1_e {
        @include flex(1);
    }

    %global_flex_1_auto_e {
        @include flex(1 auto);
    }

    %global_flex_0_auto_e {
        @include flex(0 auto);
    }

    %global_flex_100_e {
        @include flex(1 100%);
    }

    %global_flex_0_100_e {
        @include flex(0 100%);
    }

    %global_flex_0_50_e {
        @include flex(0 50%);
    }

    %border_box {
        -moz-box-sizing: border-box;
        box-sizing: border-box;
    }

    %transition_all {
        @include transition;
    }

    %clearfix {
        &:before,
        &:after {
            content: " ";
            display: table;
        }

        &:after {
            clear: both;
        }
    }

    %uri_size_repeat {
        background-size: contain !important;
        background-repeat: no-repeat !important;
    }

    %translate3d {
        -webkit-transform: translateZ(0);
        -moz-transform: translateZ(0);
        transform: translateZ(0);
        -webkit-transform-style: preserve-3d;
        -moz-transform-style: preserve-3d;
        transform-style: preserve-3d;
        -webkit-backface-visibility: hidden;
        -moz-backface-visibility: hidden;
        backface-visibility: hidden;
    }

    %backface {
        -webkit-backface-visibility: hidden; // Chrome and Safari */
        -moz-backface-visibility: hidden; // Firefox */
        -ms-backface-visibility: hidden; // Internet Explorer */
        backface-visibility: hidden;
    }

    %no-user-select {
        -webkit-user-select: none; // Chrome all / Safari all */
        -moz-user-select: none; // Firefox all */
        -ms-user-select: none; // IE 10+ */
        // No support for these yet, use at own risk */
        -o-user-select: none;
        user-select: none;
    }

    %butt_halo_v1 {
        &:after {
            $w: 7px;
            z-index: -1;
            position: absolute;
            top: -$w;
            left: -$w;
            right: -$w;
            bottom: -$w;
            content: "";
            border-radius: 30px;
            @include vert_grad(#dbdbd1, #fffffe);
        }
    }

    %wrapper_devider {
        position: relative;
        @include clearfix;

        &:before {
            position: absolute;
            width: 1px;
            top: 0;
            bottom: 0;
            content: "";
        }
    }

    %main_block_shadow {

        &:before {
            $box_w: 150px;
            $box_w2: 500%;
            $box_color: rgba(200, 216, 228, .9);
            z-index: -2;
            position: absolute;
            top: 50%;
            bottom: 5px;
            left: $box_w;
            right: $box_w;
            content: "";
            //@include box-shadow(0 0 $box_w/2 $box_color, 0 0 $box_w/2 $box_color);
            box-shadow: 0 0 $box_w/2 $box_color, 0 0 $box_w/2 $box_color;
            @include border_radius_shadow($box_w2, $box_w);
        }

        &:after {
            $p: 40px;
            position: absolute;
            height: 1px;
            left: $p;
            right: $p;
            bottom: 0;
            content: "";
            @include ie_8_check {
                background-color: rgb(192, 210, 225);
            }
            @include ie_9_check {
                background: url(data:image/svg+xml;base64,);
            }
            @include all_check {
                background: -moz-linear-gradient(left, rgba(192, 210, 225, 0) 0%, rgba(192, 210, 225, 1) 35%, rgba(192, 210, 225, 1) 50%, rgba(192, 210, 225, 1) 65%, rgba(192, 210, 225, 0) 100%);
                background: -webkit-gradient(linear, left top, right top, color-stop(0%, rgba(192, 210, 225, 0)), color-stop(35%, rgba(192, 210, 225, 1)), color-stop(50%, rgba(192, 210, 225, 1)), color-stop(65%, rgba(192, 210, 225, 1)), color-stop(100%, rgba(192, 210, 225, 0)));
                background: -webkit-linear-gradient(left, rgba(192, 210, 225, 0) 0%, rgba(192, 210, 225, 1) 35%, rgba(192, 210, 225, 1) 50%, rgba(192, 210, 225, 1) 65%, rgba(192, 210, 225, 0) 100%);
                background: -o-linear-gradient(left, rgba(192, 210, 225, 0) 0%, rgba(192, 210, 225, 1) 35%, rgba(192, 210, 225, 1) 50%, rgba(192, 210, 225, 1) 65%, rgba(192, 210, 225, 0) 100%);
                background: -ms-linear-gradient(left, rgba(192, 210, 225, 0) 0%, rgba(192, 210, 225, 1) 35%, rgba(192, 210, 225, 1) 50%, rgba(192, 210, 225, 1) 65%, rgba(192, 210, 225, 0) 100%);
                background: linear-gradient(to right, rgba(192, 210, 225, 0) 0%, rgba(192, 210, 225, 1) 35%, rgba(192, 210, 225, 1) 50%, rgba(192, 210, 225, 1) 65%, rgba(192, 210, 225, 0) 100%);
            }
        }
    }

    %vert_centr {
        top: 50%;
        @include transform_translate(0, -50%);
    }

    %hor_centr {
        left: 50%;
        @include transform_translate(-50%, 0);
    }

    %hor_vert_centr {
        top: 50%;
        left: 50%;
        @include transform_translate(-50%, -50%);
    }

    %hover_show {
        @include opacity(0);
        @include transition(opacity, .3s);
    }

    %hover_show_parent {
        &:hover,
        &:focus {
            %hover_show {
                @include opacity(1);
            }
        }
    }

    %butt_shadow_v1 {
        //@include box-shadow(inset 0 0 2px rgba(255, 255, 255, .2), 0 1px 3px rgba(0, 0, 0, .33));
        box-shadow: inset 0 0 2px rgba(255, 255, 255, .2), 0 1px 3px rgba(0, 0, 0, .33);

        &:active {
            //@include box-shadow(inset 0 2px 5px rgba(114, 114, 114, .94) !important);
            box-shadow: inset 0 2px 5px rgba(114, 114, 114, .94) !important;
        }
    }

    %reservation_object_temp {
        @extend %clearfix;
        z-index: 1;
        position: relative;
        display: block;
        min-height: 110px;
        padding: 12px 0;
        border-top: 1px solid #b9c1c6;
        border-bottom: 1px solid #e2ecf1;
        box-shadow: inset 0 0 0 1px #fff;
        background-color: #f8f9f9;
        @include vert_grad_full(#f8f9f9 101.92%, #1d1d1e 251.92%);
        @include transition(all, .2s, ease-in-out);

        &:first-child {
            border-top: none !important;
        }

        &:last-child {
            border-bottom: none !important;
        }

        &:hover,
        &:focus {
            z-index: 10;
            border-color-top: #b9c1c6;
            background: #fff;
            box-shadow: 0 -1px 0 rgba(0, 0, 0, .18), 0 0 24px rgba(37, 136, 173, .19);
        }
    }

    %object_picture_w_temp {
        z-index: 5;
        padding: 4px;
        border: 1px solid #c3d1d9;
        background-color: #fff;

        &:before,
        &:after {
            z-index: 2;
            position: absolute;
            display: none;
            width: 68px;
            height: 68px;
            top: -2px;
            left: -2px;
            content: "";
        }
    }

}
